import React, { useState } from "react";
import styles from "./login.module.less";
import { Link } from "react-router-dom";
import { request } from "@/utils/network/request";
function Registry() {
    const [name, setName] = useState("");
    const [realName, setRealName] = useState("");
    const [passWord, setPassWord] = useState("");
    const [rePassWord, setRePassWord] = useState("");
    const [mobile, setMobile] = useState("");
    const doRegister = () => {
        request({
            method: "post",
            url: "/carnia/user/register",
            data: JSON.stringify({
                accountName: name,
                realName: realName,
                password: passWord,
                repassword: rePassWord,
                mobile: mobile,
            }),
            withCredentials: true,
        }).then(({ data }) => {
            if (data.code > 202) {
                alert(data.msg);
            }
            else {
                alert(data.msg);
            }
        });
    };
    const form = [
        {
            label: "username",
            val: name,
            fn: setName,
        },
        {
            label: "realName",
            val: realName,
            fn: setRealName,
        },
        {
            label: "passWord",
            val: passWord,
            fn: setPassWord,
        },
        {
            label: "rePassWord",
            val: rePassWord,
            fn: setRePassWord,
        },
        {
            label: "mobile",
            val: mobile,
            fn: setMobile,
        },
    ];
    return (React.createElement("div", { className: styles.registry_page },
        React.createElement("div", { className: styles.flex_wrapper },
            React.createElement("div", { className: styles.form_wrapper }, form.map(item => {
                return (React.createElement("div", { className: styles.item_wrapper, key: item.label },
                    React.createElement("span", { className: styles.label },
                        item.label,
                        ":"),
                    React.createElement("input", { className: styles.input, type: "text", onChange: e => item.fn(e.target.value), defaultValue: item.val })));
            })),
            React.createElement("button", { className: styles.btn, onClick: doRegister }, "register")),
        React.createElement(Link, { to: "/" }, "\u8FD4\u56DE")));
}
// {
//   "accountName": "lq",
//   "realName": "lq",
//   "password": "123",
//   "repassword": "123",
//   "mobile": "110"
// }
export default Registry;
