export class OnionFactory {
    middlewareList = [];
    use(middleware) {
        if (Object.prototype.toString.call(middleware) === "[object Array]") {
            this.middlewareList.push(...middleware);
        }
        else {
            this.middlewareList.push(middleware);
        }
    }
    dispatch(index, ctx) {
        if (!this.middlewareList[index]) {
            return Promise.resolve(ctx);
        }
        return Promise.resolve(this.middlewareList[index](ctx, async () => this.dispatch(index + 1, ctx)));
    }
    compose() {
        return (ctx) => {
            return this.dispatch(0, ctx);
        };
    }
}
