import { request } from "@/utils/network/request";
export const login = (data) => {
    return request({
        url: "/carnia/user/login",
        data,
    });
};
export const logout = () => {
    return request({
        url: "/carnia/user/logout",
    });
};
export const getUserInfo = () => {
    return request({
        url: "/carnia/user/getUserInfo",
    });
};
