import { isProd } from "@/utils/env";
import axios from "axios";
export const axiosMiddleware = async (ctx, next) => {
    console.log(process.env.NODE_ENV);
    const instance = axios.create({
        baseURL: isProd ? 'https://admin.1984free.ml:3000' : 'https://localhost:3000'
    });
    ctx.res = await instance(ctx.req);
    await next();
};
