import { axiosMiddleware, errorMiddleware, resultMiddleware, timeMiddleware, headerMiddleware, loginMiddleware, } from "../middlewares";
import { OnionFactory } from "../OnionFactory";
const appRequestFactory = new OnionFactory();
appRequestFactory.use([
    resultMiddleware,
    timeMiddleware,
    errorMiddleware,
    loginMiddleware,
    headerMiddleware,
    axiosMiddleware,
]);
const requestFn = appRequestFactory.compose();
export const request = (param) => {
    param.headers = param.headers || {};
    const defaultParam = {
        withCredentials: true,
        method: "post",
    };
    const context = {
        req: Object.assign({}, defaultParam, param),
        headers: param.headers,
        res: {},
    };
    return requestFn(context);
};
