import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getUserInfo } from "@/store/asyncAction";
/**
 * 获取缓存中的用户信息
 * @param force Boolean, 如果传了true，则会请求接口
 * @returns  [userInfo, loading]
 */
export const useUserInfo = (force = false) => {
    const userInfo = useSelector(state => state.userInfo);
    const [loading, setLoading] = useState(!!force);
    const dispatch = useDispatch();
    useEffect(() => {
        if (force) {
            const p = dispatch(getUserInfo);
            p.then(() => {
                setLoading(false);
            }).catch(() => {
                setLoading(false);
            });
        }
    }, []);
    return [userInfo, loading];
};
